import React, { useState } from "react";
import { parseDate } from "@internationalized/date";
import { Calendar } from "./calendar";

const Datepicker: React.FC = () => {
  const [showCalendar, setShowCalendar] = useState<"start" | "end" | null>(null);
  const [startDate, setStartDate] = useState<string | null>(null);
  const [endDate, setEndDate] = useState<string | null>(null);

  const handleInputClick = (type: "start" | "end") => {
    setShowCalendar(type);
  };

  const handleDateSelect = (date: any) => {
    if (showCalendar === "start") {
      setStartDate(date.toString());
      setShowCalendar(null);
    } else if (showCalendar === "end") {
      setEndDate(date.toString());
      setShowCalendar(null);
    }
  };

  return (
    <div id="date-range-picker" className="flex items-center gap-2">
      <div className="relative">
        <div className="absolute inset-y-0 start-0 flex items-center ps-3 pointer-events-none">
          <svg
            className="w-4 h-4 text-gray-500 dark:text-gray-400"
            aria-hidden="true"
            xmlns="http://www.w3.org/2000/svg"
            fill="currentColor"
            viewBox="0 0 20 20"
          >
            <path d="M20 4a2 2 0 0 0-2-2h-2V1a1 1 0 0 0-2 0v1h-3V1a1 1 0 0 0-2 0v1H6V1a1 1 0 0 0-2 0v1H2a2 2 0 0 0-2 2v2h20V4ZM0 18a2 2 0 0 0 2 2h16a2 2 0 0 0 2-2V8H0v10Zm5-8h10a1 1 0 0 1 0 2H5a1 1 0 0 1 0-2Z" />
          </svg>
        </div>
        <input
          id="datepicker-range-start"
          name="start"
          type="text"
          className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full ps-10 p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
          placeholder="Select date start"
          autoComplete="off"
          value={startDate ? startDate : ""}
          readOnly
          onClick={() => handleInputClick("start")}
        />
        {showCalendar === "start" && (
          <div className="absolute z-10 top-12 left-0 bg-white shadow-lg rounded">
            <Calendar
              aria-label="Start Date"
              defaultValue={startDate ? parseDate(startDate) : undefined}
              onChange={(date: unknown) => handleDateSelect(date)}
            />
          </div>
        )}
      </div>
      <span className="mx-2 text-gray-500">to</span>
      <div className="relative">
        <div className="absolute inset-y-0 start-0 flex items-center ps-3 pointer-events-none">
          <svg
            className="w-4 h-4 text-gray-500 dark:text-gray-400"
            aria-hidden="true"
            xmlns="http://www.w3.org/2000/svg"
            fill="currentColor"
            viewBox="0 0 20 20"
          >
            <path d="M20 4a2 2 0 0 0-2-2h-2V1a1 1 0 0 0-2 0v1h-3V1a1 1 0 0 0-2 0v1H6V1a1 1 0 0 0-2 0v1H2a2 2 0 0 0-2 2v2h20V4ZM0 18a2 2 0 0 0 2 2h16a2 2 0 0 0 2-2V8H0v10Zm5-8h10a1 1 0 0 1 0 2H5a1 1 0 0 1 0-2Z" />
          </svg>
        </div>
        <input
          id="datepicker-range-end"
          name="end"
          type="text"
          className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full ps-10 p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
          placeholder="Select date end"
          autoComplete="off"
          value={endDate ? endDate : ""}
          readOnly
          onClick={() => handleInputClick("end")}
        />
        {showCalendar === "end" && (
          <div className="absolute z-10 top-12 left-0 bg-white shadow-lg rounded">
            <Calendar
              aria-label="End Date"
              defaultValue={endDate ? parseDate(endDate) : undefined}
              onChange={(date) => handleDateSelect(date)}
            />
          </div>
        )}
      </div>
    </div>
  );
};

export default Datepicker;